/* Homepage */
.App {
    width: 1440px;
}

/* Homepage Thumb grid */
.card-grid {
    display: grid;
    justify-items: center;
    grid-template-columns: repeat(3, 1fr);
    column-gap: 50px;
    row-gap: 50px;
    margin-top: 43px;
    margin-bottom: 43px;
    padding: 56px 50px;
    border-radius: 25px;
    background-color: #F6F6F6;
}

/* Homepage housing links */
.links-housing {
    text-decoration: none;
}

/* Homepage responsive mobil */
@media screen and (max-device-width: 375px) {
    .App {
        width: auto;
    }

    .card-grid {
        display: flex;
        flex-direction: column;
        row-gap: 20px;
        margin-top: 25px;
        margin-bottom: 100px;
        padding: 0;
        background: none;
    }
}